                  <td>
                    <div class="qtyBox" v-show="store.Enable === '1'">
                      <div class="reduce" :class="{qtyDisabled:item2.Qty < 1}" @click="updateCartsAddpriceQty(item, index2, item2.Qty - 1)"><i class="fa fa-minus"></i></div>
                      <div class="number"> {{item2.Qty}} </div>
                      <div class="add" :class="{qtyDisabled:item2.Qty > item.buyQty - 1}" @click="updateCartsAddpriceQty(item, index2, item2.Qty + 1)"><i class="fa fa-plus"></i></div>
                    </div>
