              <ul :style="`left: -${allPicUlleft * liLength}px; width: ${selectProduct.allPicLength * liLength}px;`" >
                <li ref='liItem' v-for = " (item, index) in (selectProduct.imgArr) " :key="`${item}_${index}`" 
                  @click=" selectProduct.mainImgIndex = index ;"
