                <div class="price">NT$ {{item.Price}}</div>
                <div class="qtyBox" v-show="store.Enable === '1'">
                  <div class="reduce" :class="{qtyDisabled:item.Qty < 1}" @click="updateProductsAddpriceQty(selectProduct, index, item.Qty - 1)"><i class="fa fa-minus"></i></div>
                  <div class="number"> {{item.Qty}} </div>
                  <div class="add" :class="{qtyDisabled:item.Qty > selectProduct.buyQty - 1}" @click="updateProductsAddpriceQty(selectProduct, index, item.Qty + 1)"><i class="fa fa-plus"></i></div>
                </div>
                <div class="discontinued" v-show="store.Enable === '0'">停售中</div>
              </div>
            </li>
        </ul>
        </div>

        <div class="detail">
          <div class="title">商品詳情</div>
          <div class="content" v-html="unescapeHTML(selectProduct.Detail)"></div>
        </div>
      </div>
    </div>

    <div class="flyImg" :style="`top: ${flyImgTop}px; left: ${flyImgLeft}px`" v-if="pageFilterProduct[flyImgIndex]" v-show="showflyImg">
      <img :src="pageFilterProduct[flyImgIndex].Img1" alt="">
    </div>
    <div class="cartIcon" :class="{cartIconScale:cartIconScale}"  v-show="showPage === 'main'" @click="showPage = 'cart'; getTotal()">
      <i class="fa fa-shopping-cart fa-2x" aria-hidden="true"></i>
      <div class="num">
        {{cartsLength}}
      </div>
    </div>

    <div class="message_container">
      <div v-for="item in  messageArr" :key="item.id" 
          class="message" :class="{ messageActive:item.messageActive, messagefadeout:item.messagefadeout}" 
          :ref="`m${item.id}`" 
          :style="`background:${item.isSuccess ? 'rgba(50, 200, 50, 0.6)' : 'rgba(255, 0, 0, 0.6)'}`"> 
          {{ item.messageStr }}
      </div>
    </div>

    <div class="main" v-if="showPage === 'main'">
      <div class="logo_name">
        <div class="logo" v-if="store.Logo"
          :style="{backgroundImage: `url(${store.Logo})`}">
        </div>
        <!-- <div class="name" v-if="store.Name"> {{store.Name}} </div> -->
      </div>
      <div class="categories">
        <ul>
          <li v-for="item in categories" :key="item.ID" 
              :class="{categoryActive:item.ID === category}"
              @click="category = item.ID; currentPage = 1;" 
          >
            {{item.Name}}
          </li>
        </ul>
      </div>
      <div class="arrangement">
        <div>排列方式</div>
        <div class="icon"
          :class="{iconActive:arrangement==0}"
          @click="arrangement=0"
        >
          <i class="fa fa-th-large" aria-hidden="true"></i>
        </div>
        <div class="icon"
          :class="{iconActive:arrangement==1}"
          @click="arrangement=1"
        >
          <i class="fa fa-th-list" aria-hidden="true"></i>
        </div>
      </div>

      <div class="products" :class="{change:arrangement==1}">
        <ul>
          <li v-for="(item, index) in pageFilterProduct" :key="item.ID" >
            <div class="pic" ref='picWidth' :style="{backgroundImage :`url(${item.Img1})`, height:`${picHeight}px`}" @click="selectProduct = item ; selectIndex = index; showPage = 'selectProduct';getAddPrice(item.ID, item);allPicUlleft = 0">
              <div class="detailButton">查看詳情</div>
            </div>
            <div class="content">
              <div class="title">{{item.content}}</div>
              <div class="title">{{item.Name}}</div>
              <div class="price" style="color:#9e9e9e; text-decoration: line-through; font-size:14px">NT$ {{item.Price}}</div>
              <div class="price">NT$ {{item.NowPrice}}</div>
              <div class="qtyBox" v-show="store.Enable === '1'">
                <div class="reduce" :class="{qtyDisabled:item.buyQty<1}" @click="updateProductsBuyQty(index, item.buyQty-1, $event)"><i class="fa fa-minus"></i></div>
                <div class="number"> {{item.buyQty}} </div>
                <div class="add" :class="{qtyDisabled:item.buyQty>9}" @click="updateProductsBuyQty(index, item.buyQty+1, $event)"><i class="fa fa-plus"></i></div>
              </div>
              <div class="discontinued" v-show="store.Enable === '0'">停售中</div>
            </div>
          </li>
        </ul>
      </div>

      <div class="pages">
        <ul>
          <li :class="{'pageDisabled':currentPage===1}" @click="pageChange(currentPage-1)">
            Previous
          </li>
    
          <li v-for="page in totalPage" :key="`page_${page}`" 
              :class="{'liActive':currentPage===page}"
              @click="pageChange(page)">
            {{page}}
          </li>
    
          <li :class="{'pageDisabled':currentPage===totalPage}" @click="pageChange(currentPage+1)">
            Next
          </li>
        </ul>
      </div>

      <div class="notice">
        <ul>
          <li v-if="store.Content" @click="showPage = 'Content'">
            訂購須知
          </li>
          <li v-if="store.Description" @click="showPage = 'Description'">
            配送須知
          </li>
          <li v-if="store.PrivacyPolicy" @click="showPage = 'PrivacyPolicy'">
            隱私權聲明
          </li>
        </ul>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      site: {},
      store: {},
      categories: [],
      products: [],

      carts: [],

      category: '',
      arrangement: 0,

      totalPage: 0,
      pageNum: 12,
      currentPage: 1,

      selectProduct: {},
      selectIndex: 1,

      showPage: 'main',

      showflyImg: 0,
      cartIconScale: 0,
      flyImgIndex: 0,
      flyImgTop: 0,
      flyImgLeft: 0,

      cartsLength: 0,

      messageArr: [],

      stepIndex: 1,

      total: {},

      isSame: false,
      transport: 1,
      info: {
        purchaser_email:'',
        purchaser_name:'',
        purchaser_number:'',
        receiver_name:'',
        receiver_number:'',
        receiver_address:'',
        info_message:''
      },
      orderIng: false,

      allPicUlleft: 0,
      liLength: 120,

      api:'192.168.6.239:5052',
      // api:'192.168.80.239:8080',
  
      discountCode: '',
      useCodeSuccess: '',

      innerHeight: 0,
      picHeight: 0,
    }
  },
  watch:{
    isSame(v){
      if(v){
        this.info.receiver_name = this.info.purchaser_name;
        this.info.receiver_number = this.info.purchaser_number;
      } else {
        this.info.receiver_name = ''
        this.info.receiver_number = '';
      }
    },
  },
  computed:{
    filterProducts() {
      const vm = this;
      let arr = [];
      if(vm.category === '0'){
        arr = vm.products;
      } else {
        vm.products.forEach(p => {
          if(p.categoryArr){
            for(let i = 0; i < p.categoryArr.length; i++){
              if(p.categoryArr[i] === vm.category){
                arr.push(p);
                break;
              }
            }
          }
        })
      }
      vm.totalPage = Math.ceil(arr.length / vm.pageNum);
      return arr;
    },
    pageFilterProduct(){
      let arr = [];
      let start;
      this.filterProducts.forEach((p,i)=> {
        start = (this.currentPage - 1) * this.pageNum;
        if( start <= i &&  i <= start + this.pageNum - 1 ){
          arr.push(p);
        }
      })
      return arr;
    },
  },
  methods: {
    login(func){
      this.site = JSON.parse(localStorage.getItem('site')) || [] ;
      let o = `site=${this.site.Site}&store=${this.site.Name}`;
      const config = {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      };
      const url = `http://${this.api}/interface/store/UserLogin`
      this.$http.post(url, o, config).then((res) => {
        // console.log('login: ');
        // console.log(res.data);
        func();
      });
    },
    getSite(){
      const vm = this;
      const url = `http://${vm.api}/interface/store/GetSite`;
      this.$http.get(url).then((res) => {
        if(res.data.errormessage){
          // console.log('login => getSite');
          vm.login(vm.getSite);
          return;
        }
        // console.log('site: ');
        // console.log(res.data.data[0]);
        vm.site = res.data.data[0];
        localStorage.setItem('site', JSON.stringify(vm.site));

        vm.getStore();
        vm.getCategories();
        vm.getProducts();
      });
    },
    getStore(){
      const vm = this;
      const url = `http://${vm.api}/interface/store/getStore`;
      vm.categories = [];
      this.$http.get(url).then((res) => {
        if(res.data.errormessage){
          // console.log('login => getSite');
          vm.login(vm.getStore);
          return;
        }
        // console.log('store: ');
        // console.log(res.data.data[0]);
        vm.store = res.data.data[0];
        vm.arrangement = vm.store.Sort || "0";
        document.title = vm.store.Name;
      });
    },
    getCategories() {
      const vm = this;
      const url = `http://${vm.api}/interface/store/GetCategory`;
      vm.categories = [];
      this.$http.get(url).then((res) => {
        // console.log('categories: ');
        // console.log(res.data.data);
        vm.categories =[{ID: "0", Name: "所有分類商品", Show: "1"}, ...res.data.data];
      });
    },
    getProducts() {
      const vm = this;
      const url = `http://${vm.api}/interface/store/storeLogin`;
      vm.products = [];
          
      this.$http.get(url).then(async (res) => {
        // console.log('products: ');
        // console.log(res.data.data);
        vm.products = res.data.data;
        for(let i = 0; i < vm.products.length; i++){
          let p = vm.products[i];
          vm.$set(p, 'buyQty', 0);
          vm.$set(p, 'mainImgIndex', 0);
          vm.$set(p, 'imgArr', [p.Img1, p.Img2, p.Img3, p.Img4, p.Img5]);
          vm.$set(p, 'categoryArr', [p.Category1, p.Category2, p.Category3, p.Category4, p.Category5]);
          let c = 0;
          for(let j = 0; j < p.imgArr.length; j++){
            if(p.imgArr[j]){
              c++
            }
          }
          vm.$set(p, 'allPicLength', c);
        }
        vm.category = '0';
        vm.getCarts();
      })
    },
    getAddPrice(id, item, i, qty){
      const vm = this;
      const url = `http://${vm.api}/interface/store/GetAdditional`;
      const params = `id=${id}`;
      const config = {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      };
      vm.$http.post(url, params, config).then((res) => {
        let data = res.data.data;
        data.forEach(d => {
          d.Qty = 0;
        })
        vm.$set(item, 'addPrice', data);
        vm.getCarts();

        if(qty){
          item.addPrice[i].Qty = qty;
        } else {
          vm.computedLiLength();
        }
      });
    },
    getCarts(){
      this.carts = JSON.parse(localStorage.getItem(`${this.site.Site}@carts`)) || [];
      for( let i = 0; i < this.carts.length; i++){
        for ( let j = 0; j < this.products.length; j++){
          if( this.carts[i].ID === this.products[j].ID){
            this.products[j].buyQty = this.carts[i].buyQty;
            if(this.carts[i].addPrice){
              for( let k = 0; k < this.carts[i].addPrice.length; k++){
                if(this.products[j].addPrice){
                  this.products[j].addPrice[k].Qty = this.carts[i].addPrice[k].Qty;
                }
              }
            }
          }
        }
      }
      this.computedCartsLength();
    },
    setCarts(){
      this.computedCartsLength();
      localStorage.setItem(`${this.site.Site}@carts`, JSON.stringify(this.carts));
      this.getTotal();
    },
    
    createCartsStr(){
      let o = {
        'id': '',
        'price': '',
        'qry': '',
        'additionalid':'',
        'additionalprice':'',
        'additionalqry':''
      };
      this.carts.forEach(function(c,i){
        o.id += ( o.id ==='' ? `${c.ID}`  : `,${c.ID}` );
        o.price += ( o.price ==='' ? `${c.NowPrice}`  : `,${c.NowPrice}` );
        o.qry += ( o.qry ==='' ? `${c.buyQty}`  : `,${c.buyQty}` );
        let f = true;
        if(c.addPrice){
          for( let j = 0; j < c.addPrice.length; j++){
            let item = c.addPrice[j];
            if(item.Qty){
              o.additionalid += ( o.additionalid === "" ? `${item.ID}`  : `,${item.ID}` );
              o.additionalprice += ( o.additionalprice === "" ? `${item.Price}`  : `,${item.Price}` );
              o.additionalqry += ( o.additionalqry === "" ? `${item.Qty}`  : `,${item.Qty}` );
              f = false;
            }
          }
        }
      })
      return o;
    },
    getTotal(){
      const vm = this;
      let o = vm.createCartsStr();
      if( !o.id ){
        return;
      }
      // console.log(o);
      vm.total = {};
      const url = `http://${vm.api}/interface/store/GetProductTotal?`;
      const params = `id=${o.id}&qry=${o.qry}&additionalid=${o.additionalid}&additionalqry=${o.additionalqry}&code=${vm.useCodeSuccess}`;
      const config = {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      };
      this.$http.post(url, params, config).then((res) => {
        if(res.data.errormessage){
          // console.log('login => getTotal');
          vm.login(vm.getTotal);
          return;
        }
        // console.log('total: ');
        // console.log(res.data.data[0]);
        vm.total = res.data.data[0];
      });
    },
    checkOrder(){
      const vm = this;
      if(vm.orderIng){
        return;
      } else {
        vm.$validator.validate().then((result) => {
          if (result && vm.transport !== '0') {
            if(vm.useCodeSuccess){
              const url = `http://${vm.api}/interface/store/DiscountCodeCheckEmail`;
              const params = `code=${vm.discountCode}&email=${vm.info.purchaser_email}`;
              const config = {
                headers: {
                  'Content-Type': 'application/x-www-form-urlencoded'
                }
              };
              this.$http.post(url, params, config).then((res) => {
                if(res.data.data[0].status === '0'){
                  let c = confirm("該mail已使用過折扣碼，按確定取消折扣碼優惠直接完成訂單，按取消重新輸入email或折扣碼");
                  if (c) {
                    vm.discountCode = '';
                    vm.useCodeSuccess = '';
                    vm.total.Sum = vm.total.Sum*1 + vm.total.DiscountCode*1;
                    vm.total.DiscountCode = 0;
                    vm.createOrder();
                    // console.log('有使用折扣碼，email用過折扣碼，取消折扣碼直接完成訂單');
                  } else {
                    // console.log('有使用折扣碼，email用過折扣碼，重新輸入email或折扣碼');
                  }
                } else {
                  vm.createOrder();
                  // console.log('有使用折扣碼，email沒用過折扣碼');
                }
              });
            } else {
              vm.createOrder();
              // console.log('沒有使用折扣碼');
            }
          }
        });
      }
    },
    createOrder(){
      this.orderIng = true;
      let o = this.createCartsStr();

      let url = `http://192.168.6.239:5052/Line/LinePayRequest`;
      // let url = `http://${ this.api.split(':')[1] === '5052' ? this.api.split(':')[0]+':5050' : this.api.split(':')[0] }/Line/LinePayRequest`;

      let params = `Site=${this.site.Site}&StoreName=${this.site.Name}&ProductIdList=${o.id}&PriceList=${o.price}&AmountList=${o.qry}&ExtraProductIdList=${o.additionalid}&ExtraPriceList=${o.additionalprice}&ExtraAmountList=${o.additionalqry}&SendWay=${this.transport}&Email=${this.info.purchaser_email}&Name=${this.info.purchaser_name}&Phone=${this.info.purchaser_number}&Receiver=${this.info.receiver_name}&ReceiverPhone=${this.info.receiver_number}&Address=${this.info.receiver_address}&Message=${this.info.info_message}&Discount=${this.total.Discount * 1}&Shipping=${this.total.Shipping * 1}&Total=${this.total.Sum * 1}&DiscountPrice=${this.total.DiscountCode}&DiscountCode=${this.useCodeSuccess}`;
      const config = {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      };
      this.$http.post(url, params, config).then((res) => {
        if(res.data.success){
          this.showMessage('', false)
          this.orderIng = false;
          return;
        }
        this.orderIng = false;
        // console.log('order: ');
        // console.log(params);
        window.open(res.data.payUrl);
        this.carts = [];
        this.setCarts();
        this.info = {
          purchaser_email:'',
          purchaser_name:'',
          purchaser_number:'',
          receiver_name:'',
          receiver_number:'',
          receiver_address:'',
          info_message:''
        };
        this.discountCode = '';
        this.useCodeSuccess = '';
        this.showPage = 'main';
        this.stepIndex= 1;
        this.getProducts();
      });
    },

    computedCartsLength(){
      let addPriceLength = 0;

      for(let i = 0; i < this.carts.length; i++){
        if(this.carts[i].addPrice){
          for(let j = 0; j < this.carts[i].addPrice.length; j++){
            if(this.carts[i].addPrice[j].Qty){
              addPriceLength += 1;
            }
          }
        }
      }
      this.cartsLength = this.carts.length + addPriceLength;
    },

    
    // carts.buyQty change => update products => set carts
    updateCartsBuyQty(i, qty){
      let vm = this;
      qty = qty < 0 ? 0 : (qty > 10 ? 10 : qty);

      let item = vm.carts[i];
      item.buyQty = qty;
      
      vm.products.forEach((product, index)=>{
        if(product.ID === vm.carts[i].ID){
          product.buyQty = qty;
        }
      })

      vm.carts.forEach((item, index)=>{
        if(item.buyQty === 0){
          vm.carts.splice(index, 1);
        }
      })
      vm.setCarts();

      if(item.addPrice){
        for(let j = 0; j < item.addPrice.length; j++){
          if(item.addPrice[j].Qty > item.buyQty){
            vm.updateCartsAddpriceQty(item, j, item.buyQty);
          }
        }
      }
    },
    // pageFilterProduct.buyQty change => update carts => set carts
    updateProductsBuyQty(i, qty, e){
      let vm = this;
      qty = qty < 0 ? 0 : (qty > 10 ? 10 : qty);

      let isAdd = qty - vm.pageFilterProduct[i].buyQty;
      if(isAdd === 0){
        return;
      }

      // update pageFilterProduct ( bind view )
      vm.$set(vm.pageFilterProduct[i], 'buyQty', qty);

      // update carts
      let k = false;
      let changing = vm.pageFilterProduct[i];
      vm.carts.forEach((item, index)=>{
        if(item.ID === changing.ID){
          k = true;
          vm.carts[index].buyQty = changing.buyQty;
        }
      })
      if(k===false){
        vm.carts.push(JSON.parse(JSON.stringify(changing)));
      }
      vm.carts.forEach((item, index)=>{
        if(item.buyQty === 0){
          vm.carts.splice(index, 1);
        }
      })

      // set carts
      vm.setCarts();

      // fly
      if(e){
        vm.flyHandler(i,isAdd, e);
      }

      if(vm.pageFilterProduct[i].addPrice){
        for(let j = 0; j < vm.pageFilterProduct[i].addPrice.length; j++){
          vm.updateProductsAddpriceQty(vm.pageFilterProduct[i], j, vm.pageFilterProduct[i].addPrice[j].Qty);
        }
      }
    },

    // Addprice : carts.buyQty change => update products => set carts 
    // item: 商品, i: 加價購索引 
    updateCartsAddpriceQty(item, i, qty){
      let vm = this;
      qty = qty < 0 ? 0 : (qty > item.buyQty ? item.buyQty : qty);

      item.addPrice[i].Qty = qty;


      vm.products.forEach((product, index)=>{
        if(product.ID === item.ID){
          if(!product.addPrice){
            vm.getAddPrice(product.ID, product, i, qty);
          } else {
            product.addPrice[i].Qty = qty;
          }
        }
      })
      vm.setCarts();
    },
    // Addprice : pageFilterProduct.buyQty change => update carts => set carts
    updateProductsAddpriceQty(item, i, qty){
      let vm = this;
      qty = qty < 0 ? 0 : (qty > item.buyQty ? item.buyQty : qty);
      // update pageFilterProduct ( bind view )
      vm.$set(item.addPrice[i], 'Qty', qty);

      // update carts
      let changing = item;
      vm.carts.forEach((item, index)=>{
        if(item.ID === changing.ID){
          if(!vm.carts[index].addPrice){
            vm.carts[index].addPrice =  [...changing.addPrice];
          }
          vm.carts[index].addPrice[i].Qty = changing.addPrice[i].Qty;
        }
      })

      // set carts
      vm.setCarts();
    },


    flyHandler(i,isAdd,{pageX, pageY}){
      // 設定 flyImg 起點 終點 
      let start;
      let end;

      // cartIcon 
      var scrollHeight = window.pageYOffset ||
      document.documentElement.scrollTop ||
      document.body.scrollTop || 0;
      let cartIcon = document.querySelector('.cartIcon');
      let cartIconTop = cartIcon.offsetTop + scrollHeight + 12.5;
      let cartIconLeft = cartIcon.offsetLeft + 12.5;

      if(isAdd > 0){
        start = {x:pageX, y:pageY};
        end = {x:cartIconLeft, y:cartIconTop};
      } else {
        start = {x:cartIconLeft, y:cartIconTop};
        end = {x:pageX, y:pageY};
      }

      // 起點顯示 flyImg
      let flyImg = document.querySelector('.flyImg');
      this.flyImgIndex = i;
      this.flyImgTop = start.y;
      this.flyImgLeft = start.x;
      // flyImg.style.top = start.y + 'px';
      // flyImg.style.left = start.x + 'px';
      this.showflyImg = 1;

      // 設定 intervalTimes
      let intervalTimes = 50;
      let moveX = (end.x - start.x)/intervalTimes;
      let moveY = (end.y - start.y)/intervalTimes;

      // fly
      if(isAdd < 0){
        this.cartIconScaleHandler();
      }
      let vm = this;
      var interval = setInterval(function(){
        intervalTimes-=1;

        start.x += moveX;
        start.y += moveY;
        vm.flyImgTop = start.y;
        vm.flyImgLeft = start.x;

        if(intervalTimes < 1){
          clearInterval(interval);
          vm.showflyImg = 0;
          if(isAdd > 0){
            vm.cartIconScaleHandler();
          }
        }
      }, 10);
    },
    cartIconScaleHandler(){
      let vm = this;
      vm.cartIconScale = 1;
      setTimeout(function(){
        vm.cartIconScale = 0;
      },200);
    },

    pageChange(p){
      p = p < 1 ? 1 : (p > this.totalPage ? this.totalPage : p);
      this.currentPage = p;
    },

    showMessage(messageStr, isSuccess){
      let id = new Date().getTime();
      this.messageArr.push({
        id,
        messageStr,
        isSuccess,
        messageActive: false,
        messagefadeout: false
      })
      setTimeout(()=>{
        this.messageArr.forEach((m, i)=>{
          if (m.id === id){
            this.messageArr[i].messageActive = true;
          }
        })
        setTimeout(()=>{
          this.messageArr.forEach((m, i)=>{
            if (m.id === id){
              this.messageArr[i].messagefadeout = true;
            }
          })
          setTimeout(()=>{
            this.messageArr.forEach((m, i)=>{
              if (m.id === id){
                this.messageArr.splice(i, 1);
              }
            })
          }, 500)
        }, 5000)
      }, 100)
    },

    pInput(){
      if(this.isSame){
        this.info.receiver_name = this.info.purchaser_name;
        this.info.receiver_number = this.info.purchaser_number;
      }
    },
    unescapeHTML(a){
      a = "" + a;
      return a.replace(/&lt;/g, "<").replace(/&gt;/g, ">").replace(/&amp;/g, "&").replace(/&quot;/g, '"').replace(/&apos;/g, "'");
    },
    computedLiLength(){
      this.liLength = 0;
      this.liLength = this.$refs.picLength.clientWidth / 3;
      this.$refs.liItem.forEach(li => {
        li.style.width = this.liLength - 10 + 'px';
      });
    },
    discount(){
      const vm = this;
      if(!vm.discountCode){
        vm.showMessage('請輸入折扣碼', false);
        return;
      }
      const url = `http://${vm.api}/interface/store/CheckDiscountCode`;
      const params = `code=${vm.discountCode}`;
      const config = {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      };
      vm.$http.post(url, params, config).then((res) => {
        let data = res.data.data[0].status;
        if(data==='1'){
          data = '套用折扣碼成功';
        } else if (data==='0'){
          data = '查無此折扣碼';
        } else if (data ==='2'){
          data = '折扣碼已用完';
        }
        vm.showMessage(data, data === '套用折扣碼成功' ? true : false);
        if (data === '套用折扣碼成功') {
          vm.useCodeSuccess = vm.discountCode;
          vm.getTotal();
        } else {
          vm.discountCode = '';
          vm.useCodeSuccess = '';
          vm.getTotal();
        }
      });
    },
  },
  created(){
    let isUsed2 = JSON.parse(localStorage.getItem('isUsed2')) || false ;
    if(!isUsed2){
      localStorage.removeItem(`carts`);
      localStorage.removeItem(`isUsed`);
      localStorage.removeItem(`${this.site.Site}@carts`);
      localStorage.setItem('isUsed2', JSON.stringify(true));
    }
    this.getSite();
  },
  mounted(){
    this.innerHeight = window.innerHeight;
  }
}
</script>

